import { useSupabaseClient } from '@supabase/auth-helpers-react';
import { useEffect, useState } from 'react';
import { Database } from '../types/database.types';
import { GlassCard } from './GlassCard';

export default function SecurityTab() {
  const supabase = useSupabaseClient<Database>();
  const [authLogs, setAuthLogs] = useState<any[]>([]);
  const [threats, setThreats] = useState<any[]>([]);

  useEffect(() => {
    const fetchAuthLogs = async () => {
      const { data, error } = await supabase
        .from('auth_logs')
        .select('*')
        .order('created_at', { ascending: false })
        .limit(50);
      if (!error && data) setAuthLogs(data);
    };

    const fetchThreats = async () => {
      const { data, error } = await supabase
        .from('security_events')
        .select('*')
        .order('created_at', { ascending: false });
      if (!error && data) setThreats(data);
    };

    fetchAuthLogs();
    fetchThreats();
  }, [supabase]);

  return (
    <div className="space-y-4">
      <GlassCard>
        <div>
          <h3 className="text-xl font-bold mb-2">Authentication Logs</h3>
          <div className="max-h-96 overflow-y-auto">
            {authLogs.map((log) => (
              <div key={log.id} className="p-2 border-b">
                {log.event_type} - {log.ip_address} - {new Date(log.created_at).toLocaleString()}
              </div>
            ))}
          </div>
        </div>
      </GlassCard>

      <GlassCard>
        <div>
          <h3 className="text-xl font-bold mb-2">Threat Detection</h3>
          <div className="h-64">
            {threats.length > 0 ? (
              threats.map((threat) => (
                <div key={threat.id} className="p-2 border-b">
                  {threat.severity} - {threat.description}
                </div>
              ))
            ) : (
              <div>No threats detected</div>
            )}
          </div>
        </div>
      </GlassCard>
    </div>
  );
}
